.main {
  flex-direction: column;
  flex: 1 1 auto;
}

.container {
  padding-left: 15px;
  padding-right: 15px;
  @media screen and (min-width: 480px) {
    margin-left: auto;
    margin-right: auto;
    width: 480px;
  }

  @media screen and (min-width: 768px) {
    width: 760px;
  }

  @media screen and (min-width: 1200px) {
    width: 1200px;
  }
}
